declare global {
  interface Window {
    Telegram: {
      WebApp: any;
    };
  }
}

const tg = typeof window !== 'undefined' ? window.Telegram?.WebApp : null;

export function useTelegram() {
  const onClose = () => {
    tg?.close();
  };

  const onShare = () => {
    tg?.switchInlineQuery('    ! ');
  };

  const onBuy = async () => {
    console.log('Starting payment flow...');
    console.log('Telegram object:', tg);
    
    if (!tg) {
      console.error('Telegram WebApp is not available');
      return false;
    }
    
    try {
      console.log('Showing payment popup...');
      
      //  
      console.log('Available methods:', {
        showPopup: typeof tg.showPopup,
        MainButton: tg.MainButton,
        sendData: typeof tg.sendData
      });

      //    MainButton
      if (tg.MainButton) {
        console.log('Using MainButton for payment...');
        tg.MainButton.setText('  1 Star');
        tg.MainButton.show();
        tg.MainButton.onClick(() => {
          console.log('MainButton clicked');
          tg.sendData(JSON.stringify({
            action: 'buy',
            amount: 1,
            currency: 'STARS'
          }));
        });
        return true;
      }

      //   MainButton,   showPopup
      console.log('Trying showPopup...');
      const result = await tg.showPopup({
        title: ' ',
        message: '  - 1 Star',
        buttons: [
          {
            type: 'buy',
            text: '  1 Star',
            params: {
              amount: 1,
              currency: 'STARS'
            }
          },
          { type: 'cancel', text: '' }
        ]
      });

      console.log('Popup result:', result);
      return result?.button_id === 'buy';
    } catch (err) {
      const error = err as Error;
      console.error('Payment error:', error);
      console.log('Error details:', {
        name: error?.name || 'Unknown error',
        message: error?.message || 'No message',
        stack: error?.stack || 'No stack trace'
      });
      return false;
    }
  };

  return {
    onClose,
    onShare,
    onBuy,
    tg,
    user: tg?.initDataUnsafe?.user,
  };
}
